import jss from 'jss';
import jssGlobal from 'jss-plugin-global';
import jssNested from 'jss-plugin-nested';

export function setMultiSelectItemFontSize(baseCls: string, scale: number) {
    const cls = `${baseCls}-${BI.UUID()}`;

    const selector = `.${cls} .bi-checkbox .checkbox-content:after`;

    jss.use(jssGlobal(), jssNested());

    jss.createStyleSheet({
        '@global': {
            [`${selector}`]: {
                'border-width': `${2 * scale}px`,
                width: `${6 * scale}px`,
                height: `${8 * scale}px`,
                top: `${5 * scale}px`,
                left: `${2 * scale}px`,
            },
        },
    }).attach();

    return cls;
}
